const customIcon = new Icon({
  // iconUrl: "https://cdn-icons-png.flaticon.com/512/447/447031.png",
  iconUrl: require("./icons/ic.png"),
  iconSize: [50, 50], // size of the icon
});

const houseIcon = new Icon({
  // iconUrl: "https://cdn-icons-png.flaticon.com/512/447/447031.png",
  iconUrl: require("./icons/ic1.png"),
  iconSize: [38, 38], // size of the icon
});

const activeIcon = new Icon({
  // iconUrl: "https://cdn-icons-png.flaticon.com/512/447/447031.png",
  iconUrl: require("./icons/ic4.png"),
  iconSize: [38, 38], // size of the icon
});

const checkedIcon = new Icon({
  // iconUrl: "https://cdn-icons-png.flaticon.com/512/447/447031.png",
  iconUrl: require("./icons/ic3.png"),
  iconSize: [38, 38], // size of the icon
});

const center = [43.69595470427469, 7.2455224091905395];

function App() {
  const [houses, setHouses] = useState(myData.houses);
  const [sortingColumn, setSortingColumn] = useState("id");
  const [isSally, setIsSally] = useState(false);
  const [open, setOpen] = useState(false);
  const [openBackup, setOpenBackup] = useState(false);
  const [selectedValue, setSelectedValue] = useState(1);
  const [toShow, setToShow] = useState(-1);

  useEffect(() => {
    async function getHouses() {
      let data = "";

      let config = {
        method: "get",
        maxBodyLength: Infinity,
        url: `https://getpantry.cloud/apiv1/pantry/${PANTRY_ID}/basket/houses`,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      await axios
        .request(config)
        .then(function (response) {
          setHouses(response.data.houses);
        })
        .catch(function (error) {
          terror();
        });
    }
    getHouses();
  }, []);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClickOpenBackup = () => {
    setOpenBackup(true);
  };

  const handleCloseBackup = (value) => {
    setOpenBackup(false);
    setSelectedValue(value);
  };

  const handleClose = (value) => {
    setOpen(false);
    setSelectedValue(value);
  };

  const handelAdminChange = (e) => {
    if (e.target.value == PASSWORD) {
      setIsSally(true);
      handleClickOpenBackup();
      tsally();
    }
  };

  const handleLike = async (id) => {
    let h = [...houses];
    let i = 0;
    for (i = 0; i < h.length; i++) {
      if (h[i].id == id) break;
    }
    h[i]["liked"] = h[i]["liked"] ? false : true;
    setHouses(h);

    const basket = { length: h.length, houses: h };

    const data = JSON.stringify(basket);

    await axios
      .post(
        `https://getpantry.cloud/apiv1/pantry/${PANTRY_ID}/basket/houses`,
        data,
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then(function (response) {})
      .catch(function (error) {
        terror();
      });
  };

  const handleCheck = async (id) => {
    let h = [...houses];
    let i = 0;
    for (i = 0; i < h.length; i++) {
      if (h[i].id == id) break;
    }
    h[i]["checked"] = h[i]["checked"] ? false : true;
    setHouses(h);

    const basket = { length: h.length, houses: h };

    const data = JSON.stringify(basket);

    await axios
      .post(
        `https://getpantry.cloud/apiv1/pantry/${PANTRY_ID}/basket/houses`,
        data,
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then(function (response) {})
      .catch(function (error) {
        terror();
      });
  };
  const maisons = sortHouses(houses, sortingColumn);

  return (
    <main>
      <ToastContainer />
      <MapContainer center={center} zoom={13}>
        {/* OPEN STREEN MAPS TILES */}
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker position={center} icon={customIcon}>
          <Popup>UNI</Popup>
        </Marker>

        {maisons.map((house) => (
          <Marker
            key={house.id}
            position={house.coordinates}
            icon={
              toShow == house.id
                ? activeIcon
                : house.checked
                ? checkedIcon
                : houseIcon
            }
          >
            <Popup>
              <Card sx={{ maxWidth: 150, maxHeight: 150 }}>
                <CardContent>
                  {formatDateddmm(house.date)}
                  <br></br>
                  <a href={house.anounce} target="__blank">
                    Voire annonce
                  </a>
                  <br></br>
                  {house.note}
                </CardContent>
              </Card>
            </Popup>
          </Marker>
        ))}
      </MapContainer>
      <div className="up_center">
        <Chip
          avatar={<Avatar alt="Natacha" src={require("./icons/ic1.png")} />}
          label="Nouveau"
          variant="outlined"
        />
        <Chip
          avatar={<Avatar alt="Natacha" src={require("./icons/ic3.png")} />}
          label="
          Vrifi par Sally"
          variant="outlined"
        />
        <Chip
          avatar={<Avatar alt="Natacha" src={require("./icons/ic4.png")} />}
          label="Slectionn"
          variant="outlined"
        />
      </div>
      <div className="center">
        <TextField
          id="standard-basic"
          label="Admin Only"
          variant="standard"
          onChange={handelAdminChange}
        />
        {isSally ? (
          <BackupDialog
            selectedValue={selectedValue}
            open={openBackup}
            onClose={handleCloseBackup}
          />
        ) : null}
        <div className="sorting">
          <p>trier par : </p>
          <Button
            variant="contained"
            onClick={() => {
              setSortingColumn("distance");
            }}
          >
            Distance
          </Button>
          <Button
            variant="outlined"
            onClick={() => {
              setSortingColumn("id");
            }}
          >
            id
          </Button>
        </div>
        <div className="dialog">
          <Button variant="outlined" onClick={handleClickOpen}>
            ajouter <FontAwesomeIcon icon={faHouse} style={{ marginLeft: 5 }} />
          </Button>
          <SimpleDialog
            selectedValue={selectedValue}
            open={open}
            onClose={handleClose}
          />
        </div>
      </div>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell align="right">ID</TableCell>
              <TableCell align="right">Distance</TableCell>
              <TableCell align="right">Liens</TableCell>
              <TableCell align="right">Google Maps</TableCell>
              <TableCell align="right">Jour/Mois</TableCell>
              <TableCell align="right">Heure</TableCell>
              <TableCell align="right">Ajout par</TableCell>
              {isSally ? <TableCell align="right">Liked</TableCell> : null}
              {isSally ? <TableCell align="right">Checked</TableCell> : null}
            </TableRow>
          </TableHead>
          <TableBody>
            {houses.map((house) => (
              <TableRow
                key={house.id}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                <TableCell align="right">
                  <a
                    onClick={() => setToShow(house.id)}
                    style={{ cursor: "pointer" }}
                  >
                    {house.id}
                  </a>
                </TableCell>
                <TableCell component="th" scope="row">
                  {`${getDistanceFromLatLonInKm(
                    center[0],
                    center[1],
                    house.coordinates[0],
                    house.coordinates[1]
                  )}Km`}
                </TableCell>
                <TableCell align="right">
                  <a href={house.anounce} target="__blank">
                    Lien
                  </a>
                </TableCell>
                <TableCell align="right">
                  <a
                    href={
                      house.coordinates[0] == 33.773603646684585 &&
                      house.coordinates[1] == -7.159932383597909
                        ? "/"
                        : formatGoogleMaps(house.coordinates)
                    }
                    target={
                      house.coordinates[0] == 33.773603646684585 &&
                      house.coordinates[1] == -7.159932383597909
                        ? ""
                        : "__blank"
                    }
                  >
                    Google Maps
                  </a>
                </TableCell>
                <TableCell align="right">
                  {formatDateddmm(house.date)}
                </TableCell>
                <TableCell align="right">
                  {formatDatehhmm(house.date)}
                </TableCell>
                <TableCell align="right">
                  <Chip
                    avatar={<Avatar>{house.creater[0]}</Avatar>}
                    label={house.creater}
                  />
                </TableCell>
                {isSally ? (
                  <TableCell align="right">
                    <a onClick={() => handleLike(house.id)}>
                      <FontAwesomeIcon
                        icon={house.liked ? solid : faHeart}
                        style={{ color: "#b56bff", cursor: "pointer" }}
                      />
                    </a>
                  </TableCell>
                ) : null}
                {isSally ? (
                  <TableCell align="right">
                    <a onClick={() => handleCheck(house.id)}>
                      <FontAwesomeIcon
                        icon={house.checked ? faCheck : faO}
                        style={{ color: "#b56bff", cursor: "pointer" }}
                      />
                    </a>
                  </TableCell>
                ) : null}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </main>
  );
}

export default App;
